* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-item {
  display: flex;
  text-align: center;
}

.country-list {
  text-decoration: none;
}

.country-name {
  margin-left: 20px;
  font-size: 20px;
  color: blue;
}


.image-item {
  margin-right: 20px;
}

.name {
  font-size: 30px;
  
  margin-bottom: 20px;
}

.country-info_item {
  color: #051458;
}

.country-info_item {
  
  list-style: none;
}